/*TO TOP BUTTON*/    
.to-top a {
  background-color: #0b2029 !important;
  transition: ease all 400ms;
  border: 0;
  color: #fff !important;
  overflow: hidden;
}
.to-top a:hover {
  background: #009de0 !important;
}
.to-top a:hover .mdi::before {
  animation: btp-anim 1.5s ease infinite;
}

@keyframes btp-anim {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  98% {
    transform: translateY(-110%);
    opacity: 1;
  }
  99% {
    opacity: 0;
    transform: translateY(-110%);
  }
  100% {
    transform: translateY(0);
    opacity: 0.5;
  }
}
